
.modalwindow{
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.6);
border: 10px solid #ff6904;
background-color: #ffffff;

width: calc(100% - 30px);

position: absolute;

top: 463px;
right: 15px;

display:none; 
z-index:9999;

}





.modalwindow-content_specification{
    
    font-size: 16px;
font-weight: 700;
line-height: 36px;
text-transform: uppercase;
font-family: 'Montserrat';
color: #000000;
margin: 100px 80px 100px 155px;

}

.modalwindow-content_specification li{
    display: flex;
    justify-content: space-between;
    text-align: left;
   
   
}
.modalwindow-content_specification li div{
    width: 50%;
    margin-right: 10px;
   
}



.modalwindow-content_specification ul li{
    border-bottom: 1px solid #cccccc;

}

.modalwindow-content img{
   padding:  10px;
}

.modalwindow-close{
    text-align: right;
    
}

.modalwindow-close img{
    margin: 25px;
    cursor: pointer;
}

@media screen and (max-width: 940px){

.modalwindow-content_specification{
    margin: 50px;
}
}

@media screen and (max-width: 700px){

    .modalwindow-content_specification{
        margin: 10px;
    }
    }
    