.opportunities{
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
    position: relative;
  }
  .opportunities::after{
    content:'';
    display: block;
    height: 100%;
    width: 100vw;
    position: absolute;
    top: 0;
    z-index: -2;
    margin: auto;
    left: -50vw;
    right: -50vw;
    background: url(../images/backgraund2.jpg) center center;
    background-size: cover;
  }

  .opportunities-description{
      text-align: center;
      font-size: 16px;
font-weight: 700;
text-transform: uppercase;
  }
  .red-text{
      color: #f6620f;
  }

   .paragraph{
       margin:0 15px;
       display: inline-block;
       width:10px;
  }

 .opportunities-menu{
     display: flex;
     padding: 80px 0 90px 0;
 }

 .opportunities-menu-list{
     padding: 45px 0;
     position: relative;
     width: 355px;
     min-width: 355px;
 }

 .opportunities-menu-item{
     padding-right: 40px;
        font-size: 16px;
        line-height: 36px;
        display: flex;
        align-items: baseline;
    background: url(../images/paragraph.png) no-repeat center left; 
    padding-left: 25px;
 }

 .opportunities-menu-item.active{
    background: url(../images/check.png) no-repeat center left; 
 }

 .opportunities-menu-item:hover{
     cursor: pointer;
 }
 .opportunities-menu-content.active{
    background-color: rgba(0,0,0, 0.7) ;
     border: 1px solid #f6620f;
     padding: 20px 15px 50px 60px;
     margin-left: -1px;
 }

 .menu-active-name{
    font-family: "Montserrat-Bold";
    font-weight: 700;
    line-height: 24px;
    text-transform: uppercase;
    margin-top: 20px;
    position: relative;
 }

 .menu-active-name::before{
    content:'';
     background: url(../images/paragraph_red.png);
     width: 10px;
     height: 10px;
     position: absolute;
     top: 7px;
     right: 102%;
 }

 .opportunities-menu-item.active{
    background-color: rgba(0,0,0, 0.7) ;
    color: #f6620f;
    border: 1px solid transparent;
    border-image: linear-gradient(to right, transparent 0%,#f6620f 50%, #f6620f 99.8%, #000 99.8%, #000 100%);
    border-image-slice: 1;
 }


 .opportunities-menu-content {
    display: none; /* по умолчанию прячем все блоки */
    }
    .opportunities-menu-content.active {
    display: block; /* по умолчанию показываем нужный блок */
    }

 @media screen and (max-width: 940px){
    .paragraph{
        margin:0 10px 0 0;
   }
 
    .opportunities-menu-list{
        width: 320px;
        min-width: 320px;
    }
    .opportunities-menu-active{
        padding: 15px 15px 15px 40px;
    }

    .menu-active-name{
        margin-top: 10px;
    }
 }


 @media screen and (max-width: 600px){
    .opportunities-menu{
        flex-wrap: wrap;
        padding: 0 0 70px 0;
 }
 .opportunities-menu-active{
     margin: 40px 0;
 }

 .opportunities-menu-list{
     display: flex;
    width: 100% !important;
    overflow: hidden;
     overflow-x:  scroll;
     white-space: nowrap;
 }

  .opportunities-menu-item{
     margin: 0 20px;  
 }
 .opportunities-menu-item.active{
     border: none;
     background-color: inherit;
 }

 .opportunities-menu-content.active{
    padding: 5px 5px 5px 25px;
 }
}
@media screen and (max-width: 450px){
    .opportunities-menu-active{
        padding: 10px 10px 10px 30px;
        font-size: 12px;
    }
}
