.buy-kassa {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
    position: relative;
    text-align: center;
    
    background-size: contain;
   
  }
  .buy-kassa::after{
    content:'';
    display: block;
   height: 100%;
    width: 100vw;
    position: absolute;
    top: 0;
    z-index: -2;
    margin: auto;
    left: -50vw;
    right: -50vw;
    background: url(../images/backgraund3.jpg) center  ;
    background-size: cover;
    
    
    
   
   
    
  }

 

  
  
  
   
   
  
  

  .background-kassa-max::after{
    content:'';
    display: block;
    height: 100%;
    width: 100vw;
    position: absolute;
    top: 0;
    z-index: -2;
    margin: auto;
    left: -50vw;
    right: -50vw;
    background: url(../images/backgraund4.jpg)  center center;

    background-size: cover;
  }

  .buy-kassa-description{
    font-family: 'Montserrat-ExtraBold';
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    padding: 70px 0 50px 0;
  }
  .switch-buy-kassa{
    border: 1px solid  #3f3f3f;
    background: #ffffff;
    color: black;
  }

  .switch-button-buy-kassa.active{
    background: #3f3f3f;
  }

  .switch-button-buy-kassa.active::after{
    background: #3f3f3f;
  }

  .buy-kassa-name{
    font-family: 'Montserrat-ExtraBold';
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    padding: 50px 0 70px 0;
    position: relative;
  }

  .buy-kassa-name::after{
      position: absolute;
      content: '';
      background: url(../images/dotted-line.png);
      background-size: contain;
      background-repeat: no-repeat;
      width: 160px;
    height: 70px;
    top:55px;
    margin-left: 40px;
}                                    

  
  .buy-kassa-full-construction{
      
      background: #f6620f;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
border: 1px solid #ffffff;
border-bottom: none;
padding: 45px 60px 40px 60px;
margin: 0 70px;
font-family: 'Montserrat-ExtraBold';
font-size: 16px;
font-weight: 400;
text-transform: uppercase;

  }

  .buy-kassa-check{
      display: flex;
      justify-content: space-between;
      
  }
.check-img{
    display: flex;
    flex-direction: column;
    
   
  
}
.kassamax_check{
  max-width: 125%;
}
  .check-kassa{
      display: flex;
     
      justify-content: space-between;
      width: 560px;
      
  }


  .check-kassa_choice{
    position: relative;
    display: block;
  }

  
  .check-kassa_choice  input[type="radio"] {
      position: absolute;
      top: -1%;
    left: 0%;
    border: 1px solid #ffffff;
    border-radius: 50%;
    background:#f6620f; /* Переключатель выключен */
    height: 30px; /* Высота картинки */
    width: 30px; /* Ширина картинки */
    -webkit-appearance: none; /* Прячем чекбокс */
    cursor: pointer; /* Курсор в виде руки */
   }

   .check-kassa_choice input:checked {
       background: #3f3f3f;
       background-image: url(../images/check_white.png);
       background-repeat: no-repeat;
       background-position: center;
   }

   .check-kassa_button{
     display: inline-block;
    border-radius: 35px;
    border: 1px solid #ffffff;
    background-color: rgba(255, 105, 4, .5);
    font-family: 'Montserrat';
font-size: 16px;
font-weight: 700;
line-height: 36px;
text-transform: uppercase;
padding: 15px 35px;
margin-bottom: 30px;
width: 265px;
min-width: 260px;
  }

  .check-kassa_choice input:checked + .check-kassa_button {
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
border-radius: 35px;
border: 2px solid #ffffff;
background-color: #ff6904;
   }

   .check-kassa_button:hover {
       cursor: pointer;
       
    

  }

  
  .buy-button{
    border-radius: 25px;
    border: 2px solid #ffffff;
    background-color: #3f3f3f;
    font-family: 'Montserrat-ExtraBold';
font-size: 14px;
font-weight: 400;
text-transform: uppercase;
color: #ffffff;
padding: 15px 50px;
box-shadow: 0 0 30px #3f3f3f;
margin: 25px 0;
  }

  .buy-button:hover{
      cursor: pointer;
  }
  

  .check-kassa_buy{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 30px 0;
  }

  .price {
    
    color: #ffffff;
font-family: 'Montserrat-ExtraBold';
font-size: 36px;
font-weight: 400;
margin: 55px 0;

    
}

.buy-kassa-minmax, .buy-kassa-description {
    display: none; /* по умолчанию прячем все блоки */
    }
    .buy-kassa-minmax.active, .buy-kassa-description.active {
    display: block; /* по умолчанию показываем нужный блок */
    }


    @media screen and (max-width: 1030px){
        .check-img{
            margin-top: 100px;
        }
        .buy-kassa-full-construction{
            margin: 0;
        }
    }

    @media screen and (max-width: 900px){
        .buy-kassa-check{
            flex-direction: column;
            align-items: center;
        }
        .buy-kassa-name::after{
            display: none;
        }

        .check-img{
            margin: 0;
        }
    }

    @media screen and (max-width: 600px){
        .check-kassa, .check-kassa_buy{
           flex-direction: column;
           align-items: center;
        }

        .buy-kassa-full-construction{
            padding: 5px;
        }

        .check-kassa_buy, .price{
            margin: 0;
        }

        .buy-kassa-name{
            padding: 40px 0;
        }
    }

    @media screen and (max-width: 400px){
        .switch{
            width: 280px;
        }

        .switch-button.mini, .switch-button.max{
            padding: 10px;
        }
        
        .buy-kassa-description{
           
           font-size: 30px;
        }
    }