.header{
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
    position: relative;
  }
  .header::after{
    content:'';
    display: block;
    height: 100%;
    width: 100vw;
    position: absolute;
    top: 0;
    z-index: -2;
    margin: auto;
    left: -50vw;
    right: -50vw;
    background: url(../images/backgraund.jpg) center center;
    background-size: cover;
  }

  .header-top{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 0 15px 0;
      border-bottom: 1px solid #ff6904;
  }

  .header-top_logo{
    color: #ff6904;
    font-family: 'Montserrat-ExtraBold';
    font-size: 26px;
    font-weight: 400;
    text-transform: uppercase;
  }

  .menu-list{
      display: flex;
  }

  .menu-item{
      margin: 15px;
      font-weight: 500;
      text-transform: uppercase;
  }

  .header-top__phone{
      display: flex;
      align-items: center;
      justify-content: space-between;
  }
  .header-top__phone-link{
    text-align: center;   
}
  .phone-link_number{
      display: block;
      font-family: 'Montserrat-ExtraBold';
      font-weight: 400;
      font-size: 24px;
  }

  .phone-link_text{
      text-decoration: underline;
  }

  .menu-item__link:hover, .header-top__phone-link:hover{
      color: #ff6904;
  }

  .h2{
    font-family: "Montserrat-ExtraBold";
    font-size: 48px;
    font-weight: 400;
    padding: 70px 0 30px 0;
  }

  .delivery{
      font-size: 18px;
      display: flex;
  }

  .icon-cart{
      margin-left: 30px;
  }
.header-product-description{
    margin: 135px 0 20px 0;
    display: flex;
    justify-content: space-between;
}

  .product-description-item{
      font-size: 16px;
      line-height: 30px;
      display: flex;
      align-items: baseline;
  }

  .icon-check{
      margin-right: 12px;
  }

  .header-cashbox{
      position: relative;
  }

  .header-cashbox img{
    max-width: 654px;
    width: 654px;
  }

  .header-bottom{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding-bottom: 45px;
  }

  .header-bottom_item{
  display: flex;
    
      justify-content: space-between;
      align-items: center;
      width: calc(100%/3 - 100px);
  }

  .header-bottom_item svg{
      margin-right: 12px;
  }

  .icon-emblem{
    width: 81px;
    height: 50px;
  }

  .icon-rubles{
    width: 72px;
    height: 34px;
  }

  .icon-insurance{
    width: 68px;
    height: 54px;
  }

  .icon-menu{
    width: 30px;
    height: 17px;
  }

  .header-top__menu-triger{
      display: none;
  }

  @media screen and (max-width: 1080px){

    .menu-item__link:hover{
      color: #cccccc;
      cursor: pointer;
      
    }
    .header-top__menu{
    display: none;
    position: absolute;
    z-index: 10;
    width: 100%;
    padding: 80px 0 300px 0;
    background: #ff6904;
    color: #ffffff;
    top: 0;
    right: 0;
    margin: 0;
    text-align: center;
    
    }
      .menu-list{
        flex-direction: column;
        
      }
      
     
    .header-top__menu-triger{
        display: block;
        z-index: 100;
        color: #ffffff;
    }

    .header-top__phone{
        width: 355px;
    }
    .header-cashbox img{
        max-width: 505px;
        width: 505px;
      }
      .header-bottom{
          justify-content: space-between;
      }
      .header-bottom_item{
          width: 27%;
      }
  }

  @media screen and (max-width: 936px){
    .header-product-description{
      flex-wrap: wrap;
      justify-content: center;
    }
      .header-product-description {
        margin: 100px 0 20px 0;
    }
    .header-cashbox{
      order: -1;
      margin: 15px 0;
    }
  }

  @media screen and (max-width: 670px){
    .header-bottom{
      flex-direction: column;
      align-content: center;
    }
    .header-bottom_item{
      justify-content: left;
      margin: 30px 0;
      width: calc(80% - 100px);
    }
    .header-bottom_item svg{
      width: 40px;
    }
  }
  @media screen and (max-width: 600px){
    .header-top__phone-link{
      display: none;
    }

    .header-top__phone{
      width: auto;
    }
  }
  @media screen and (max-width: 600px){
    .header-cashbox img{
      max-width: 430px;
      width: 430px;
    }
  }

  @media screen and (max-width: 440px){
    .header-cashbox img{
      max-width: 280px;
      width: 280px;
    }
    .header-product-description {
      margin: 20px 0 20px 0;
  }
  .h1{
    padding: 30px 0;
    font-size: 24px;
}
.header-bottom_item{
  margin: 15px 0;
}
  }
  
 