.aboutkassa{
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
    position: relative;
    color: #000000;
}
.aboutkassa::after{
    content:'';
    display: block;
    height: 100%;
    width: 100vw;
    position: absolute;
    top: 0;
    z-index: -2;
    margin: auto;
    left: -50vw;
    right: -50vw;
}
.aboutkassa-h2{
  color: #000000;
}

.switch{
    display: flex;
  margin: 0 auto;
 
    justify-content: center;
    border: 1px solid  #ff6904;
    border-radius: 30px;
    width: 360px;
    
    text-transform: uppercase;
    font-family: 'Montserrat-ExtraBold';
    text-align: center;

}



.switch-button.mini{
    width: 50%;
    padding: 10px 30px;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
   
}

.switch-button.max{
    width: 50%;
    padding: 10px 30px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;

}

.switch-button.active{
    background: #ff6904;
    color: white;
    position: relative;
}

.switch-button:hover{
    cursor: pointer;
}
.switch-button.active::after{
    content: '';
   position: absolute;
   height: 17px;
   width: 17px;
   top: 28px;
   left: 50%;
    background: #ff6904;
    transform: rotate(45deg);
}



.aboutkassa-tagline{
    font-size: 16px;
font-weight: 700;
text-transform: uppercase;
font-family: 'Montserrat-Bold';
text-align: center;
margin: 40px 0;
}

.aboutkassa-description{
    color: #000000;
   text-align: center;
   margin: 40px 0 60px 0;

}

.aboutkassa-specifications{
    display: flex;
    justify-content: space-between;
    padding-bottom: 175px;
}

.specification-column{
   
   text-align: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    
}

.column-centr{
     width: 45%;
    
    // margin: 0 30px;
    
    
}
.column-left, .column-rigth{
    // min-width: 250px;
    // width: 250px;
    width: 25%;
    margin-top: 55px;
    position: relative;

   
    
}


.column-left::before{
    content: '';
    position: absolute;
    height: 1px;
    width: 350px;
    background: #5a5a5a;
    transform: rotate(23deg);
    top: 190px;
    right: -277px;
    z-index: -3;
}


.column-rigth::after{
    content: '';
    position: absolute;
    height: 1px;
    width: 350px;
    background: #5a5a5a;
    transform: rotate(-23deg);
    top: 190px;
    left: -277px;
    z-index: -3;

}

.circle-img{
    border: 6px solid #5b5b5b;
    border-radius: 50%;
   
    
}
.aboutkassa-specifications_h2{
    font-size: 16px;
font-weight: 700;
text-transform: uppercase;
line-height: 36px;
font-family: 'Montserrat-Bold';
margin: 25px 0 0px 0;
}



