.numeral4-mini::after{
    content: url(../images/mini4.png);
    
    position: absolute;
    top: 24%;
    left: -32%;
}


.numeral1-max::after{
    content: url(../images/max1.png);
    position: absolute;
    top: 16%;
    left: 11%;
}

.numeral2-max::after{
    content: url(../images/max2.png);
    position: absolute;
    top: 8%;
