.texn-specification {
    background:transparent;
    border: 6px solid #ff6904;
   width: 285px;
   margin: 0 auto;
   padding: 12px 7px;
    position: relative;
   background: #ffff;
   font-family: 'Montserrat-Bold';
   font-size: 12px;
   color: #585858;

}



.texn-specification::after{
    content: '';
    position: absolute;
    border: 14px solid transparent;
    border-top: 28px solid #ff6904;
    display: block;
    width: 0;
    height: 0;
   top:100%;
   right: calc(50% - 14px);
   
}

.texn-specification::before{
    content: '';
    position: absolute;
    border: 8px solid transparent;
    border-top: 18px solid #ffff;
    display: block;
    width: 0;
    height: 0;
   top:98%;
   right: calc(50% - 8px);
  z-index:1;
}

 .texn-specification div:first-child{
    
    
font-weight: 700;
text-decoration: underline;
text-transform: uppercase;
margin-bottom: 10px;

}
.cirkle-grey-mini, .cirkle-grey-max{
    width: 326px;
height: 326px;
background-color: #6f6f6f;
border-radius: 50%;
position: relative;


}

.cirkle-grey-mini::before{
    content: '';
    background:   url(../images/kassamin.png) no-repeat;
    width: 300%;
    height: 350%;
    background-size: contain;
    
    position: absolute;
    top:-127%;
    left: -86%;
   
   z-index: 10;

    
}

.cirkle-grey-max::before{
    content: '';
    background:   url(../images/kassamax.png) no-repeat;
    width: 150%;
    height: 200%;
    background-size: contain;
    
    position: absolute;
    top:-23%;
    left: -25%;
    
}
.fine-stroke{
    width: 470px;
height: 470px;
border: 1px solid #ff6904;
border-radius: 50%;
display: flex;
    align-items: center;
    justify-content: center;
    margin: 70px 0 20px 0;
    z-index: -2;


}
.thick-stroke{
    width: 392px;
height: 392px;
border: 3px solid #ff6904;
border-radius: 50%;
display: flex;
    align-items: center;
    justify-content: center;
    z-index: -2;

}
.column-centr_bottom{
    display: flex;
    justify-content: space-between;
}

.aboutkassa-specifications {
    display: none; /* по умолчанию прячем все блоки */
    }
    .aboutkassa-specifications.active {
    display: flex;
    
     /* по умолчанию показываем нужный блок */
    }

 @media screen and (max-width: 1030px){
     .column-centr_bottom{
         margin: 90px -40px 0 -40px;
     }
    

 .aboutkassa-specifications_description{
     width: 80%;
     margin: 0 auto;
 }
 
}

@media screen and (max-width: 870px){
    .cirkle-grey-mini, .cirkle-grey-max{
        width: 226px;
    height: 226px;
    }
    .fine-stroke{
        width: 370px;
    height: 370px;
    }

    .thick-stroke{
        width: 292px;
    height: 292px;
    }

    .column-centr_bottom{
        margin: 260px -40px 0 -40px;

        width: 200%;
    }

     div.aboutkassa-specifications_numeral.numeral4-max, div.aboutkassa-specifications_numeral.numeral4-mini{
         left: -23%;
     }
}


@media screen and (max-width: 730px){
    .cirkle-grey-mini, .cirkle-grey-max{
        width: 126px;
    height: 126px;
    }
    .fine-stroke{
        width: 270px;
    height: 270px;
    }

    .thick-stroke{
        width: 192px;
    height: 192px;
    }


    .column-left::before, .column-rigth::after{
        width: 200px;
        top: 150px;
    }

    .column-left::before{
        
        right: -157px;
       
    }
    
    
    .column-rigth::after{
        
        left: -157px;
        
    
    }

    .column-centr_bottom{
        margin: 360px -40px 0 -40px;
    }

    .aboutkassa-specifications_description{
        width: 105%;
    }
}

@media screen and (max-width: 600px){
.aboutkassa{
    display: none;
}
}