body {
  font-family: 'Montserrat';
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  overflow: hidden;
}
img {
  max-width: 100%;
  height: auto;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li,
figure,
input,
button {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  font-style: normal;
  color: inherit;
  cursor: pointer;
}
input:active,
input:focus,
input:hover {
  outline: 0;
  outline-offset: 0;
}
button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
  /* inherit font & color from ancestor */
  color: inherit;
  font: inherit;
  /* Normalize `line-height`. Cannot be changed from `normal` in Firefox 4+. */
  line-height: normal;
  /* Corrects font smoothing for webkit */
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  /* Corrects inability to style clickable `input` types in iOS */
  -webkit-appearance: none;
}
button:active,
button:focus,
button:hover {
  outline: 0;
  outline-offset: 0;
}
.icon {
  display: inline-block;
  vertical-align: middle;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  flex-shrink: 0;
}
.header {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
}
.header::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: -2;
  margin: auto;
  left: -50vw;
  right: -50vw;
  background: url(../images/backgraund.jpg) center center;
  background-size: cover;
}
.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0 15px 0;
  border-bottom: 1px solid #ff6904;
}
.header-top_logo {
  color: #ff6904;
  font-family: 'Montserrat-ExtraBold';
  font-size: 26px;
  font-weight: 400;
  text-transform: uppercase;
}
.menu-list {
  display: flex;
}
.menu-item {
  margin: 15px;
  font-weight: 500;
  text-transform: uppercase;
}
.header-top__phone {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-top__phone-link {
  text-align: center;
}
.phone-link_number {
  display: block;
  font-family: 'Montserrat-ExtraBold';
  font-weight: 400;
  font-size: 24px;
}
.phone-link_text {
  text-decoration: underline;
}
.menu-item__link:hover,
.header-top__phone-link:hover {
  color: #ff6904;
}
.h2 {
  font-family: "Montserrat-ExtraBold";
  font-size: 48px;
  font-weight: 400;
  padding: 70px 0 30px 0;
}
.delivery {
  font-size: 18px;
  display: flex;
}
.icon-cart {
  margin-left: 30px;
}
.header-product-description {
  margin: 135px 0 20px 0;
  display: flex;
  justify-content: space-between;
}
.product-description-item {
  font-size: 16px;
  line-height: 30px;
  display: flex;
  align-items: baseline;
}
.icon-check {
  margin-right: 12px;
}
.header-cashbox {
  position: relative;
}
.header-cashbox img {
  max-width: 654px;
  width: 654px;
}
.header-bottom {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-bottom: 45px;
}
.header-bottom_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(100%/3 - 100px);
}
.header-bottom_item svg {
  margin-right: 12px;
}
.icon-emblem {
  width: 81px;
  height: 50px;
}
.icon-rubles {
  width: 72px;
  height: 34px;
}
.icon-insurance {
  width: 68px;
  height: 54px;
}
.icon-menu {
  width: 30px;
  height: 17px;
}
.header-top__menu-triger {
  display: none;
}
@media screen and (max-width: 1080px) {
  .menu-item__link:hover {
    color: #cccccc;
    cursor: pointer;
  }
  .header-top__menu {
    display: none;
    position: absolute;
    z-index: 10;
    width: 100%;
    padding: 80px 0 300px 0;
    background: #ff6904;
    color: #ffffff;
    top: 0;
    right: 0;
    margin: 0;
    text-align: center;
  }
  .menu-list {
    flex-direction: column;
  }
  .header-top__menu-triger {
    display: block;
    z-index: 100;
    color: #ffffff;
  }
  .header-top__phone {
    width: 355px;
  }
  .header-cashbox img {
    max-width: 505px;
    width: 505px;
  }
  .header-bottom {
    justify-content: space-between;
  }
  .header-bottom_item {
    width: 27%;
  }
}
@media screen and (max-width: 936px) {
  .header-product-description {
    flex-wrap: wrap;
    justify-content: center;
  }
  .header-product-description {
    margin: 100px 0 20px 0;
  }
  .header-cashbox {
    order: -1;
    margin: 15px 0;
  }
}
@media screen and (max-width: 670px) {
  .header-bottom {
    flex-direction: column;
    align-content: center;
  }
  .header-bottom_item {
    justify-content: left;
    margin: 30px 0;
    width: calc(80% - 100px);
  }
  .header-bottom_item svg {
    width: 40px;
  }
}
@media screen and (max-width: 600px) {
  .header-top__phone-link {
    display: none;
  }
  .header-top__phone {
    width: auto;
  }
}
@media screen and (max-width: 600px) {
  .header-cashbox img {
    max-width: 430px;
    width: 430px;
  }
}
@media screen and (max-width: 440px) {
  .header-cashbox img {
    max-width: 280px;
    width: 280px;
  }
  .header-product-description {
    margin: 20px 0 20px 0;
  }
  .h1 {
    padding: 30px 0;
    font-size: 24px;
  }
  .header-bottom_item {
    margin: 15px 0;
  }
}
.opportunities {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
}
.opportunities::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: -2;
  margin: auto;
  left: -50vw;
  right: -50vw;
  background: url(../images/backgraund2.jpg) center center;
  background-size: cover;
}
.opportunities-description {
  text-align: center;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}
.red-text {
  color: #f6620f;
}
.paragraph {
  margin: 0 15px;
  display: inline-block;
  width: 10px;
}
.opportunities-menu {
  display: flex;
  padding: 80px 0 90px 0;
}
.opportunities-menu-list {
  padding: 45px 0;
  position: relative;
  width: 355px;
  min-width: 355px;
}
.opportunities-menu-item {
  padding-right: 40px;
  font-size: 16px;
  line-height: 36px;
  display: flex;
  align-items: baseline;
  background: url(../images/paragraph.png) no-repeat center left;
  padding-left: 25px;
}
.opportunities-menu-item.active {
  background: url(../images/check.png) no-repeat center left;
}
.opportunities-menu-item:hover {
  cursor: pointer;
}
.opportunities-menu-content.active {
  background-color: rgba(0, 0, 0, 0.7);
  border: 1px solid #f6620f;
  padding: 20px 15px 50px 60px;
  margin-left: -1px;
}
.menu-active-name {
  font-family: "Montserrat-Bold";
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  margin-top: 20px;
  position: relative;
}
.menu-active-name::before {
  content: '';
  background: url(../images/paragraph_red.png);
  width: 10px;
  height: 10px;
  position: absolute;
  top: 7px;
  right: 102%;
}
.opportunities-menu-item.active {
  background-color: rgba(0, 0, 0, 0.7);
  color: #f6620f;
  border: 1px solid transparent;
  border-image: linear-gradient(to right, transparent 0%, #f6620f 50%, #f6620f 99.8%, #000 99.8%, #000 100%);
  border-image-slice: 1;
}
.opportunities-menu-content {
  display: none;
  /* по умолчанию прячем все блоки */
}
.opportunities-menu-content.active {
  display: block;
  /* по умолчанию показываем нужный блок */
}
@media screen and (max-width: 940px) {
  .paragraph {
    margin: 0 10px 0 0;
  }
  .opportunities-menu-list {
    width: 320px;
    min-width: 320px;
  }
  .opportunities-menu-active {
    padding: 15px 15px 15px 40px;
  }
  .menu-active-name {
    margin-top: 10px;
  }
}
@media screen and (max-width: 600px) {
  .opportunities-menu {
    flex-wrap: wrap;
    padding: 0 0 70px 0;
  }
  .opportunities-menu-active {
    margin: 40px 0;
  }
  .opportunities-menu-list {
    display: flex;
    width: 100% !important;
    overflow: hidden;
    overflow-x: scroll;
    white-space: nowrap;
  }
  .opportunities-menu-item {
    margin: 0 20px;
  }
  .opportunities-menu-item.active {
    border: none;
    background-color: inherit;
  }
  .opportunities-menu-content.active {
    padding: 5px 5px 5px 25px;
  }
}
@media screen and (max-width: 450px) {
  .opportunities-menu-active {
    padding: 10px 10px 10px 30px;
    font-size: 12px;
  }
}
.aboutkassa {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
  position: relative;
  color: #000000;
}
.aboutkassa::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: -2;
  margin: auto;
  left: -50vw;
  right: -50vw;
}
.aboutkassa-h2 {
  color: #000000;
}
.switch {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  border: 1px solid #ff6904;
  border-radius: 30px;
  width: 360px;
  text-transform: uppercase;
  font-family: 'Montserrat-ExtraBold';
  text-align: center;
}
.switch-button.mini {
  width: 50%;
  padding: 10px 30px;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}
.switch-button.max {
  width: 50%;
  padding: 10px 30px;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
}
.switch-button.active {
  background: #ff6904;
  color: white;
  position: relative;
}
.switch-button:hover {
  cursor: pointer;
}
.switch-button.active::after {
  content: '';
  position: absolute;
  height: 17px;
  width: 17px;
  top: 28px;
  left: 50%;
  background: #ff6904;
  transform: rotate(45deg);
}
