.texn-specification {
  background: transparent;
  border: 6px solid #ff6904;
  width: 285px;
  margin: 0 auto;
  padding: 12px 7px;
  position: relative;
  background: #ffff;
  font-family: 'Montserrat-Bold';
  font-size: 12px;
  color: #585858;
}
.texn-specification::after {
  content: '';
  position: absolute;
  border: 14px solid transparent;
  border-top: 28px solid #ff6904;
  display: block;
  width: 0;
  height: 0;
  top: 100%;
